<template>
    <div>
        <!--header banner begin-->
        <header-banner :bannerImg="single_product.product_pic">
        <template v-slot:header-banner-content>
            <h1 class="text-h1 font-weight-thin mb-4 text-capitalize">
                {{breadcrumbs_items[1].text}}
            </h1>

            <v-breadcrumbs
                class="justify-center banner-breadcrumbs"
                :items="breadcrumbs_items"
                large
                divider="-"
            >
                <template v-slot:item="{ item }">
                <v-breadcrumbs-item
                    :href="item.href"
                    class="text-center text-capitalize"
                    :disabled="item.disabled"
                >
                    {{ item.text }}
                </v-breadcrumbs-item>
                </template>
            </v-breadcrumbs>

        </template>
        </header-banner>
        <!--header banner end-->

        <!--product spec begin-->
        <v-sheet
        color="white"
        class="px-15 py-10"
        >
            <v-row>
                <v-col
                cols="12"
                md="6"
                sm="12"
                :class="(!onLoading) ? 'd-flex align-center justify-center' : ''"
                >

                    <v-skeleton-loader
                    v-if="onLoading"
                    v-bind="skeletonAttrs"
                    type="image"
                    ></v-skeleton-loader>

                    <v-img
                        v-else
                        :src="single_product.product_pic"
                        :height="470"
                        class="grey lighten-2 rounded"
                    >
                        <template v-slot:placeholder>
                        <v-row
                            class="fill-height ma-0"
                            align="center"
                            justify="center"
                        >
                            <v-progress-circular
                            indeterminate
                            color="grey lighten-5"
                            ></v-progress-circular>
                        </v-row>
                        </template>
                    </v-img>
                </v-col>
                <v-col
                cols="12"
                md="6"
                sm="12"
                class="d-flex align-center justify-center"
                >
                    <div class="product-spec-box">

                        <v-skeleton-loader
                        v-if="onLoading"
                        v-bind="skeletonAttrs"
                        type="list-item-two-line"
                        ></v-skeleton-loader>

                        <div class="product-spec" v-else>
                            <h3 class="black--text text-h3">{{single_product.product_name}}</h3>
                            <h5 class="primary--text text-h5 mt-5">
                                {{
                                (single_product.product_price != 0) ?
                                new Intl.NumberFormat('de-DE', { style: 'currency', currency: 'EUR' }).format(single_product.product_price) :
                                'Free'
                                }}
                            </h5>
                        </div>
                        
                        <v-skeleton-loader
                        v-if="onLoading"
                        v-bind="skeletonAttrs"
                        type="actions"
                        ></v-skeleton-loader>

                        <div class="d-flex align-center mt-5" v-else>
                            <v-text-field
                            class="productCount-input"
                            v-model="product_count"
                            type="number"
                            min="0"
                            outlined
                            rounded
                            hide-details="auto"
                            append-outer-icon="mdi-plus"
                            @click:append-outer="increment_productCount"
                            prepend-icon="mdi-minus"
                            @click:prepend="decrement_productCount"></v-text-field>
                            <v-hover
                            v-slot="{ hover }"
                            >
                            <v-btn
                                dark
                                rounded
                                :color="hover ? 'primary white--text' : ''"
                                class="pa-6 my-3 transition-fast-in-fast-out font-weight-bold"
                            >
                                Add to cart
                            </v-btn>
                            </v-hover>
                        </div>

                        <v-skeleton-loader
                        v-if="onLoading"
                        v-bind="skeletonAttrs"
                        type="text"
                        ></v-skeleton-loader>

                        <div class="product-category mt-5" v-else>
                            <h6 class="black--text text-h6"><strong>Category : </strong>{{single_product.product_category}}</h6>
                        </div>

                        <v-skeleton-loader
                        v-if="onLoading"
                        v-bind="skeletonAttrs"
                        type="list-item-avatar-three-line"
                        ></v-skeleton-loader>

                        <div class="author-spec w-fit mt-5" v-else>
                            <v-card
                                dark
                                class="rounded-pill"
                            >
                                <div class="d-flex flex-wrap justify-space-between align-center">
                                    <v-avatar
                                        class="mx-2"
                                        size="100"
                                        tile
                                    >
                                        <v-img
                                            :src="(single_product.seller_avatar) ? single_product.seller_avatar : 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRy7IjIMdj4Ng7zU1TcsryLgG5uZAGXWgVuwg&usqp=CAU'"
                                            class="grey lighten-2 rounded-circle"
                                            v-ripple
                                        >
                                            <template v-slot:placeholder>
                                            <v-row
                                                class="fill-height ma-0"
                                                align="center"
                                                justify="center"
                                            >
                                                <v-progress-circular
                                                indeterminate
                                                color="grey lighten-5"
                                                ></v-progress-circular>
                                            </v-row>
                                            </template>
                                        </v-img>
                                    </v-avatar>
                                    <div>
                                        <v-card-title
                                        class="text-h5">
                                        {{single_product.seller_name}}
                                        </v-card-title>

                                            <v-card-text>
                                                <!--author instagram begin-->
                                                <v-hover
                                                v-slot="{ hover }"
                                                >
                                                <v-btn
                                                    small
                                                    v-if="single_product.seller_instagram"
                                                    icon
                                                    :color="hover ? 'primary' : ''"
                                                    target="_blank"
                                                    :href="`https://www.instagram.com/${single_product.seller_instagram}/`"
                                                    class="transition-fast-in-fast-out font-weight-bold text-center"
                                                >
                                                    <v-icon>
                                                    mdi-instagram
                                                    </v-icon>
                                                </v-btn>
                                                </v-hover>
                                                <!--author instagram end-->

                                                <!--author email begin-->
                                                <v-hover
                                                v-slot="{ hover }"
                                                >
                                                <v-btn
                                                    small
                                                    v-if="single_product.seller_email"
                                                    icon
                                                    :color="hover ? 'primary' : ''"
                                                    target="_blank"
                                                    :href="`mailto:${single_product.seller_email}`"
                                                    class="transition-fast-in-fast-out font-weight-bold text-center"
                                                >
                                                    <v-icon>
                                                    mdi-gmail
                                                    </v-icon>
                                                </v-btn>
                                                </v-hover>
                                                <!--author email end-->

                                                <!--author facebook begin-->
                                                <v-hover
                                                v-slot="{ hover }"
                                                >
                                                <v-btn
                                                    small
                                                    v-if="single_product.seller_facebook"
                                                    icon
                                                    :color="hover ? 'primary' : ''"
                                                    target="_blank"
                                                    :href="`http://www.facebook.com/${single_product.seller_facebook}`"
                                                    class="transition-fast-in-fast-out font-weight-bold text-center"
                                                >
                                                    <v-icon>
                                                    mdi-facebook
                                                    </v-icon>
                                                </v-btn>
                                                </v-hover>
                                                <!--author facebook end-->

                                                <!--author whatsapp begin-->
                                                <v-hover
                                                v-slot="{ hover }"
                                                >
                                                <v-btn
                                                    small
                                                    v-if="single_product.seller_whatsapp"
                                                    icon
                                                    :color="hover ? 'primary' : ''"
                                                    target="_blank"
                                                    :href="`https://api.whatsapp.com/send?phone=${single_product.seller_whatsapp}`"
                                                    class="transition-fast-in-fast-out font-weight-bold text-center"
                                                >
                                                    <v-icon>
                                                    mdi-whatsapp
                                                    </v-icon>
                                                </v-btn>
                                                </v-hover>
                                                <!--author whatsapp end-->
                                            </v-card-text>
                                    </div>
                                </div>
                            </v-card>
                        </div>
                    </div>
                </v-col>
            </v-row>
        </v-sheet>
        <!--product spec end-->

        <!--reviews begin-->
        <v-sheet
        color="grey lighten-2"
        class="px-15 py-10"
        >
            <sectionTitle
            :titleContent="{title:'REVIEWS',subtitle:'Be the first to review Shaping The World: Sculpture From Prehistory To Now Your email address will not be published. Required fields are marked *',hasDivider:true}"
            >
            </sectionTitle>
            
            <v-row>
                <v-col
                cols="12"
                md="6"
                sm="12"
                >
                    <div class="user-review-box">
                        <v-rating
                        v-model="user_rating"
                        background-color="grey darken-1"
                        empty-icon="$ratingFull"
                        hover
                        color="red"
                        large
                        ></v-rating>

                        <v-form class="mt-5">
                            <v-textarea
                            outlined
                            rounded
                            name="input-7-4"
                            label="Your review"
                            
                            ></v-textarea>

                            <v-text-field
                            label="Name"
                            outlined
                            rounded
                            
                            ></v-text-field>

                            <v-text-field
                            label="Email"
                            outlined
                            rounded
                            
                            ></v-text-field>

                            <v-checkbox
                            label="Save my name, email, and website in this browser for the next time I comment."
                            color="primary"
                            hide-details
                            ></v-checkbox>

                            <v-hover
                            v-slot="{ hover }"
                            >
                            <v-btn
                                rounded
                                dark
                                :color="hover ? 'primary white--text' : ''"
                                class="pa-6 my-3 transition-fast-in-fast-out font-weight-bold"
                            >
                                Submit
                            </v-btn>
                            </v-hover>
                        </v-form>
                    </div>
                </v-col>
                <v-col
                cols="12"
                md="6"
                sm="12"
                >
                    <div class="review-vector h-100 d-flex justify-center align-center">
                        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 400 300" width="406" height="306" class="illustration styles_illustrationTablet__1DWOa"><defs><linearGradient id="linear-gradient" x1="328.61" y1="210.49" x2="321.4" y2="217.7" gradientUnits="userSpaceOnUse"><stop offset="0" stop-color="gray"></stop><stop offset="1" stop-color="#fff"></stop></linearGradient></defs><title>A_</title><g style="isolation: isolate;"><g id="Layer_2" data-name="Layer 2"><path d="M18.25,165.28,153.86,86.41a6.41,6.41,0,0,1,6.41,0L363.05,202.46a6.41,6.41,0,0,1,0,11.12L233.39,288.45a20.93,20.93,0,0,1-20.93,0L18.27,176.37A6.41,6.41,0,0,1,18.25,165.28Z" fill="#f9c60b"></path><path d="M152.74,60.53,135,153l-2.57,13.36c-.36,1.89,2.53,2.69,2.89.8l17.78-92.52,2.57-13.36c.36-1.89-2.53-2.69-2.89-.8Z" fill="#42210b"></path><path d="M181,24.8h-6.23L123,49.35s14.11,103.3,14.53,103.91,7.95,3.54,7.95,3.54" fill="#f7c19c"></path><polyline points="81.91 156.1 81.91 128.75 115.02 128.75 115.02 156.1" fill="#dfdde0"></polyline><ellipse cx="98.47" cy="156.1" rx="16.55" ry="8.11" fill="#dfdde0"></ellipse><ellipse cx="98.47" cy="128.75" rx="16.55" ry="8.64" fill="#efefef"></ellipse><ellipse cx="98.47" cy="128.75" rx="13.45" ry="6.59" fill="#8cc63f"></ellipse><path d="M92.89,133.2l.51,15.26a1.24,1.24,0,0,0,1.24,1.24h.07a1.24,1.24,0,0,0,1.24-1.24V147a1.24,1.24,0,0,1,1.2-1.24h0A1.24,1.24,0,0,1,98.42,147v3.48a1.24,1.24,0,0,0,1.24,1.24h.22a1.24,1.24,0,0,0,1.24-1.24v-6.41a1.24,1.24,0,0,1,1.2-1.24h0a1.24,1.24,0,0,0,1.2-1.24l-.38-7.49A36.43,36.43,0,0,1,92.89,133.2Z" fill="#8cc63f"></path><polyline points="256.65 180.74 256.65 153.4 289.76 153.4 289.76 180.74" fill="#dfdde0"></polyline><ellipse cx="273.21" cy="180.74" rx="16.55" ry="8.11" fill="#dfdde0"></ellipse><ellipse cx="273.21" cy="153.4" rx="16.55" ry="8.64" fill="#efefef"></ellipse><ellipse cx="273.21" cy="153.4" rx="13.45" ry="6.59" fill="#f9551c"></ellipse><path d="M267.63,157.85l.51,15.26a1.24