</template>
<script>
import manageProductsApi from '@/api/manage-posts'
export default {
    name:'single-product',
    data() {
        return {
            breadcrumbs_items: [
                {
                text: 'Shop',
                disabled: false,
                href: '/user/shop-page',
                },
                {
                text: 'single product',
                disabled: false,
                href: '/single-product',
                },
            ],
            product_count: 0,
            user_rating: 0,
            single_product:{},
            skeletonAttrs: {
                class: 'my-3',
                boilerplate: false,
                elevation: 1,
            },
            onLoading: true
        }
    },
    methods: {
        increment_productCount () {
            this.product_count = parseInt(this.product_count,10) + 1
        },
        decrement_productCount () {
            if(this.product_count > 0){
                this.product_count = parseInt(this.product_count,10) - 1
            }
        },
        getSingleProduct (product_id) {
        manageProductsApi.get(`/products/${product_id}.json`)
            .then(response =>{
                this.single_product = response.data
                this.breadcrumbs_items[1].text = response.data.product_name
                this.breadcrumbs_items[1].href = `/single-product/${product_id}`
                this.onLoading = false
            }
            )
            .catch(error => console.log(error))
        },
    },
    watch:{
        '$route.params.product_id': {
            handler: function(product_id) {
                if(product_id != 'undefined' && product_id != undefined && product_id.length == 20){
                    this.getSingleProduct(product_id)
                }else if(product_id.length > 20){
                    this.$router.push({ name : 'notFound-page' })
                }
            },
            deep: true,
            immediate: true
        },
    },
}
</script>
<style scoped>
.productCount-input{
    max-width: 35%;
}
</style>